import { useEffect, useState } from 'preact/hooks';
import { Card, Typography, message } from 'antd';
// import 'antd/dist/antd.css';
import 'antd/dist/reset.css';

const { Title, Paragraph, Text } = Typography;

const HomePage = () => {
    const [data, setData] = useState({});

    useEffect(() => {
        if ('serviceWorker' in navigator) {
            navigator.serviceWorker.register('service-worker.js')
                .then(() => console.log('Service Worker registered'))
                .catch(error => console.error('Service Worker registration failed:', error));
        }

        const timestamp = new Date().getTime();
        fetch(`output/data.json?t=${timestamp}`, { cache: 'no-store' })
            .then(response => response.json())
            .then(data => setData(data))
            .catch(error => {
                console.error('Error fetching data:', error);
                message.error('Error fetching data');
            });
    }, []);

    return (
        <div>
            <div>dddd</div>
            <div id="content">
                {Object.entries(data).map(([key, modelData]) => (
                    key !== 'end' && (
                        <Card key={key} title={`Model: ${key}`} bordered={true} style={{ margin: '10px 0' }}>
                            <Paragraph>
                                <Text strong>Path:</Text> {modelData.path}
                            </Paragraph>
                            <Paragraph>
                                <Text strong>Created Time:</Text> {modelData.created_time}
                            </Paragraph>
                            <Paragraph>
                                <Text strong>Content:</Text>
                                <pre>{JSON.stringify(modelData.content, null, 2)}</pre>
                            </Paragraph>
                        </Card>
                    )
                ))}
            </div>
        </div>
    );
};

export default HomePage;